import { log } from "../utils/utils.mjs";

/**
 * Set Cache
 * @author VirgilClyne
 * @param {Map} cache - Playlists Cache / Subtitles Cache
 * @param {Number} cacheSize - Cache Size
 * @return {Boolean} isSaved
 */
export default function setCache(cache, cacheSize = 100) {
	log(`‚òëÔ∏è Set Cache, cacheSize: ${cacheSize}`, "");
	cache = Array.from(cache || []); // MapËΩ¨Array
	cache = cache.slice(-cacheSize); // ÈôêÂà∂ÁºìÂ≠òÂ§ßÂ∞è
	log(`‚úÖ Set Cache`, "");
	return cache;
};
