import { $platform, _, log } from "./utils.mjs";

export default function done(object = {}) {
    switch ($platform) {
        case "Surge":
            if (object.policy) _.set(object, "headers.X-Surge-Policy", object.policy);
            log("", `üö© ÊâßË°åÁªìÊùü! üïõ ${(new Date().getTime() / 1000 - $script.startTime)} Áßí`, "");
            $done(object);
            break;
        case "Loon":
            if (object.policy) object.node = object.policy;
            log("", `üö© ÊâßË°åÁªìÊùü! üïõ ${(new Date() - $script.startTime) / 1000} Áßí`, "");
            $done(object);
            break;
        case "Stash":
            if (object.policy) _.set(object, "headers.X-Stash-Selected-Proxy", encodeURI(object.policy));
            log("", `üö© ÊâßË°åÁªìÊùü! üïõ ${(new Date() - $script.startTime) / 1000} Áßí`, "");
            $done(object);
            break;
        case "Egern":
            log("", `üö© ÊâßË°åÁªìÊùü!`, "");
            $done(object);
            break;
        case "Shadowrocket":
        default:
            log("", `üö© ÊâßË°åÁªìÊùü!`, "");
            $done(object);
            break;
        case "Quantumult X":
            if (object.policy) _.set(object, "opts.policy", object.policy);
            // ÁßªÈô§‰∏çÂèØÂÜôÂ≠óÊÆµ
            delete object["auto-redirect"];
            delete object["auto-cookie"];
            delete object["binary-mode"];
            delete object.charset;
            delete object.host;
            delete object.insecure;
            delete object.method; // 1.4.x ‰∏çÂèØÂÜô
            delete object.opt; // $task.fetch() ÂèÇÊï∞, ‰∏çÂèØÂÜô
            delete object.path; // ÂèØÂÜô, ‰ΩÜ‰ºö‰∏é url ÂÜ≤Á™Å
            delete object.policy;
            delete object["policy-descriptor"];
            delete object.scheme;
            delete object.sessionIndex;
            delete object.statusCode;
            delete object.timeout;
            if (object.body instanceof ArrayBuffer) {
                object.bodyBytes = object.body;
                delete object.body;
            } else if (ArrayBuffer.isView(object.body)) {
                object.bodyBytes = object.body.buffer.slice(object.body.byteOffset, object.body.byteLength + object.body.byteOffset);
                delete object.body;
            } else if (object.body) delete object.bodyBytes;
            log("", `üö© ÊâßË°åÁªìÊùü!`, "");
            $done(object);
            break;
        case "Node.js":
            log("", `üö© ÊâßË°åÁªìÊùü!`, "");
            process.exit(1);
            break;
    }
}
